import React from 'react';
import { withNamespaces } from '../i18n';
import app from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

class MyPages extends React.Component {
  static async getInitialProps() {
    return {
      namespacesRequired: ['common']
    }
  }

  componentDidMount() {

    // To support email and phone number we need to validate if email first, then run the email login, otherwise use the /phone/login
    app.auth().signInWithEmailAndPassword('joakim@hejalbert.se', '332625')
      .then(function(res) {
        console.log(res.user.uid);

        app.database().ref('/users/' + res.user.uid).once('value').then(function(snap) {
          if (snap.exists()) {
              const userdata = snap.val();
              console.log(userdata);
              app.database().ref('/userData/' + res.user.uid).once('value').then(function(p) {
                console.log(p);
              });
          }
        });

      })
      .catch(function(error) {
        console.log(error);
      });
  }

  render() {
    const { t } = this.props
    return (
      <React.Fragment>
        <h1>{t('h1')}</h1>
      </React.Fragment>
    )
  }
}

export default withNamespaces('common')(MyPages)